class PageSectionItem extends HTMLElement {
    constructor() {
        // If you define a ctor, always call super() first!
        // This is specific to CE and required by the spec.
        super();
        // create shadowRoot
        const shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <style>
            :host{
                display: inline-block;
                flex: 0 1 auto;
            }
            :host(:not([flexible])){
                box-sizing: border-box;
                width: 100%;
                min-height: 100vh;
            }
            :host([flexible]){
                margin-left: 50%;
                transform: translateX(-50%);
            }
        </style>
        <slot></slot>
        `;
    }
    // set active
    setActive() {
        if(this._inView()){
            this.setAttribute('active','');
        }
    }
    // set unactive
    setUnactive() {
        if(this._inView()){
            this.removeAttribute('active','');
        }
    }
    // check if element is in view
    _inView(windowHeight){
        // get elements bounding box
        var box = this.getBoundingClientRect();
        // check if element is in view
        if( (box.top >= 0 && (
                box.top < window.innerHeight *.5 ||
                box.bottom <= window.innerHeight )) ||
            (box.top  < window.innerHeight *.5 && box.bottom > window.innerHeight*.5)
        ){
            return true;
        }
    }
}
window.customElements.define('page-section', PageSectionItem);
